"use client";

import { useState, useEffect } from 'react';
import { useSearchParams } from 'next/navigation';
import useCampaignStore from '@/store/campaignStore';
import toast from 'react-hot-toast';
import Link from 'next/link';

export default function CampaignsPage() {
  const searchParams = useSearchParams();
  const { campaigns, isLoading, getCampaigns, searchCampaigns } = useCampaignStore();
  
  const [filters, setFilters] = useState({
    category: searchParams.get('category') || '',
    search: searchParams.get('search') || '',
    sortBy: '-createdAt',
    status: 'active',
    page: 1,
    limit: 12
  });

  const categories = [
    { value: '', label: 'All Categories' },
    { value: 'medical', label: 'Medical' },
    { value: 'education', label: 'Education' },
    { value: 'disaster', label: 'Disaster Relief' },
    { value: 'animal', label: 'Animal Welfare' },
    { value: 'environment', label: 'Environment' },
    { value: 'sports', label: 'Sports' },
    { value: 'technology', label: 'Technology' },
    { value: 'other', label: 'Other' }
  ];

  const sortOptions = [
    { value: '-createdAt', label: 'Newest First' },
    { value: 'createdAt', label: 'Oldest First' },
    { value: '-currentAmount', label: 'Highest Raised' },
    { value: 'currentAmount', label: 'Lowest Raised' },
    { value: '-goalAmount', label: 'Highest Goal' }
  ];

  useEffect(() => {
    loadCampaigns();
  }, [filters]);

  const loadCampaigns = async () => {
    try {
      if (filters.search) {
        await searchCampaigns(filters.search, filters);
      } else {
        await getCampaigns(filters);
      }
    } catch (error) {
      toast.error('Failed to load campaigns');
    }
  };

  const handleFilterChange = (key, value) => {
    setFilters(prev => ({ ...prev, [key]: value, page: 1 }));
  };

  const handleSearch = (e) => {
    e.preventDefault();
    const searchValue = e.target.search.value;
    setFilters(prev => ({ ...prev, search: searchValue, page: 1 }));
  };

  const CampaignCard = ({ campaign }) => {
    const progressPercentage = Math.round((campaign.currentAmount / campaign.goalAmount) * 100);
    const daysRemaining = campaign.daysRemaining || 0;

    return (
      <Link href={`/campaigns/${campaign._id}`}>
        <div className="bg-white rounded-lg shadow-md hover:shadow-xl transition-shadow duration-300 overflow-hidden cursor-pointer">
          {/* Campaign Image */}
          <div className="relative h-48 bg-gray-200">
            {campaign.images && campaign.images[0] ? (
              <img
                src={campaign.images[0].url}
                alt={campaign.title}
                className="w-full h-full object-cover"
              />
            ) : (
              <div className="w-full h-full flex items-center justify-center bg-gradient-to-br from-green-100 to-green-200">
                <span className="text-gray-400 text-4xl">üì∑</span>
              </div>
            )}
            {campaign.isUrgent && (
              <span className="absolute top-2 right-2 bg-red-500 text-white px-3 py-1 rounded-full text-xs font-bold">
                Urgent
              </span>
            )}
            {campaign.isFeatured && (
              <span className="absolute top-2 left-2 bg-yellow-500 text-white px-3 py-1 rounded-full text-xs font-bold">
                Featured
              </span>
            )}
          </div>

          {/* Campaign Info */}
          <div className="p-4">
            {/* Category Badge */}
            <span className="inline-block bg-green-100 text-green-800 text-xs px-3 py-1 rounded-full mb-2">
              {campaign.category}
            </span>

            {/* Title */}
            <h3 className="text-lg font-bold text-gray-900 mb-2 line-clamp-2">
              {campaign.title}
            </h3>

            {/* Short Description */}
            <p className="text-gray-600 text-sm mb-4 line-clamp-2">
              {campaign.shortDescription}
            </p>

            {/* Progress Bar */}
            <div className="mb-3">
              <div className="flex justify-between text-sm mb-1">
                <span className="text-gray-600">Raised</span>
                <span className="text-green-600 font-bold">
                  {progressPercentage}%
                </span>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2">
                <div
                  className="bg-green-500 h-2 rounded-full transition-all duration-300"
                  style={{ width: `${Math.min(progressPercentage, 100)}%` }}
                ></div>
              </div>
            </div>

            {/* Stats */}
            <div className="flex justify-between items-center text-sm">
              <div>
                <span className="text-gray-900 font-bold">
                  ‚Çπ{campaign.currentAmount.toLocaleString()}
                </span>
                <span className="text-gray-500"> raised</span>
              </div>
              <div className="text-gray-600">
                of ‚Çπ{campaign.goalAmount.toLocaleString()}
              </div>
            </div>

            {/* Footer */}
            <div className="mt-4 pt-4 border-t flex justify-between items-center text-xs text-gray-500">
              <div className="flex items-center">
                <span className="mr-1">‚è±Ô∏è</span>
                <span>{daysRemaining} days left</span>
              </div>
              <div className="flex items-center">
                <span className="mr-1">üë•</span>
                <span>{campaign.analytics?.donations || 0} donors</span>
              </div>
            </div>
          </div>
        </div>
      </Link>
    );
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-gradient-to-r from-green-500 to-green-600 text-white py-12">
        <div className="container mx-auto px-4">
          <h1 className="text-4xl font-bold mb-4">Browse Fundraisers</h1>
          <p className="text-xl">Support causes that matter to you</p>
        </div>
      </div>

      {/* Filters & Search */}
      <div className="container mx-auto px-4 py-8">
        <div className="bg-white rounded-lg shadow-md p-6 mb-8">
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            {/* Search */}
            <form onSubmit={handleSearch} className="col-span-1 md:col-span-2">
              <div className="relative">
                <input
                  type="text"
                  name="search"
                  placeholder="Search campaigns..."
                  defaultValue={filters.search}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                />
                <button
                  type="submit"
                  className="absolute right-2 top-2 bg-green-500 text-white px-4 py-1 rounded-md hover:bg-green-600"
                >
                  Search
                </button>
              </div>
            </form>

            {/* Category Filter */}
            <select
              value={filters.category}
              onChange={(e) => handleFilterChange('category', e.target.value)}
              className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
            >
              {categories.map(cat => (
                <option key={cat.value} value={cat.value}>
                  {cat.label}
                </option>
              ))}
            </select>

            {/* Sort */}
            <select
              value={filters.sortBy}
              onChange={(e) => handleFilterChange('sortBy', e.target.value)}
              className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
            >
              {sortOptions.map(option => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </select>
          </div>
        </div>

        {/* Results Count */}
        <div className="mb-6">
          <p className="text-gray-600">
            {isLoading ? 'Loading...' : `Showing ${campaigns.length} campaigns`}
          </p>
        </div>

        {/* Campaign Grid */}
        {isLoading ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {[...Array(6)].map((_, i) => (
              <div key={i} className="bg-white rounded-lg shadow-md h-96 animate-pulse">
                <div className="h-48 bg-gray-300"></div>
                <div className="p-4">
                  <div className="h-4 bg-gray-300 rounded mb-2"></div>
                  <div className="h-4 bg-gray-300 rounded mb-4"></div>
                  <div className="h-2 bg-gray-300 rounded"></div>
                </div>
              </div>
            ))}
          </div>
        ) : campaigns.length > 0 ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {campaigns.map((campaign) => (
              <CampaignCard key={campaign._id} campaign={campaign} />
            ))}
          </div>
        ) : (
          <div className="text-center py-16">
            <div className="text-6xl mb-4">üì≠</div>
            <h3 className="text-2xl font-bold text-gray-800 mb-2">No campaigns found</h3>
            <p className="text-gray-600">Try adjusting your filters or search terms</p>
          </div>
        )}

        {/* Pagination */}
        {campaigns.length > 0 && (
          <div className="mt-8 flex justify-center">
            <div className="flex gap-2">
              <button
                onClick={() => handleFilterChange('page', filters.page - 1)}
                disabled={filters.page === 1}
                className="px-4 py-2 bg-gray-200 rounded-md hover:bg-gray-300 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                Previous
              </button>
              <span className="px-4 py-2 bg-green-500 text-white rounded-md">
                Page {filters.page}
              </span>
              <button
                onClick={() => handleFilterChange('page', filters.page + 1)}
                disabled={campaigns.length < filters.limit}
                className="px-4 py-2 bg-gray-200 rounded-md hover:bg-gray-300 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                Next
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

